@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);



.contact-model{
    display: flex;
    justify-content: space-between;
    width: var(--standard-width);
    margin: auto;
    height: 100%;
    padding: 2rem 0;
}

.contactRe-left{
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    width: 60%;
    justify-content: center;
    gap: 2rem;
}

.contactRe-l-top{
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

.contactRe-l-top h4{
    font-size: 16px;
    font-family: "Montserrat", sans-serif;
  
}

.contactRe-l-top p{
    font-size: 16px;
    margin: 0;
    font-family: "Montserrat", sans-serif;
    font-weight: lighter;
}

.contactRe-l-bottom{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.contactRe-info h3 {
    font-size: 14px;
    font-family: "Montserrat", sans-serif;
    font-weight: bolder;
}

.contactRe-info p {
    font-size: 14px;
    width: 11rem;
    margin: 0;
  
    font-family: "Montserrat", sans-serif;
    font-weight: lighter;
}

.contactRe-l-top h1{
    font-size: 48px;
    font-weight: 600;
    font-family: "Montserrat", sans-serif;
    
    color: #03AA6B;

}

.contactRe-right{
    width: 35%;
    margin-top: 40px;
    height: 100%;
    border: 1px solid rgb(179, 176, 176);
    padding: 2rem 1rem;
    border-radius: 2rem;
}

.contactRe-right form{
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

.contactRe-right form h2{
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 28px;
}

.contactRe-right form p{
    font-family: "Montserrat", sans-serif;
    font-weight: lighter;
}


.contactRe-right form input{
    padding: .3rem 1rem;
    margin-bottom: .5rem;
    border-radius: 2rem;
    outline: none;
    font-size: 14px;
    border: 1px solid rgb(179, 176, 176);
    background-color: transparent;
    resize: none;
}


.contactRe-right form textarea{
    padding: .5rem 1rem;
    border-radius: 1rem;
    outline: none;
    font-size: 14px;
    border: 1px solid rgb(179, 176, 176);
    resize: none;
    margin-bottom: .5rem;
}


.contactRe-right form button{
    padding: .5rem 1rem;
    background-color: #03AA6B;
    border: none;
    border-radius: 1rem;
}


.contact-map{
    margin-top: 1rem;
    height: 50rem;
}

@media (max-width: 600px) {
    .contact-model{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: 100%;
        
        height: 100%;
        padding: 16vh 4.1vw 1rem 4.1vw;
    }
    
    .contactRe-left{
        display: flex;
        flex-direction: column;
        width: 100%;
        justify-content: center;
        gap: 2rem;
        margin-bottom: 3rem;
    }
    
    .contactRe-l-top{
        display: flex;
        flex-direction: column;
        gap: .5rem;
    }
    
    .contactRe-l-top h4{
        font-size: 16px;
        font-family: "Montserrat", sans-serif;
      
    }
    
    .contactRe-l-top p{
        font-size: 16px;
        margin: 0;
        font-family: "Montserrat", sans-serif;
        font-weight: lighter;
    }
    
    .contactRe-l-bottom{
        display: flex;
        flex-wrap: wrap;
        
        justify-content: space-between;
    }
    
    .contactRe-info h3 {
        font-size: 12px;
        font-family: "Montserrat", sans-serif;
        font-weight: bolder;
    }
    
    .contactRe-info p {
        font-size: 12px;
        width: 10rem;
        margin: 0;
      
        font-family: "Montserrat", sans-serif;
        font-weight: lighter;
    }
    
    .contactRe-l-top h1{
        font-size: 32px;
        font-weight: 600;
        font-family: "Montserrat", sans-serif;
        text-align: center;
        color: #03AA6B;
    
    }
    
    .contactRe-right{
        width: 100%;
        
        height: 100%;
        border: 1px solid rgb(179, 176, 176);
        padding: 2rem 1rem;
        border-radius: 2rem;
    }
    
    .contactRe-right form{
        display: flex;
        flex-direction: column;
        gap: .5rem;
    }
    
    .contactRe-right form h2{
        font-family: "Montserrat", sans-serif;
        font-weight: 600;
        font-size: 28px;
    }
    
    .contactRe-right form p{
        font-family: "Montserrat", sans-serif;
        font-weight: lighter;
    }
    
    
    .contactRe-right form input{
        padding: .3rem 1rem;
        margin-bottom: .5rem;
        border-radius: 2rem;
        outline: none;
        font-size: 14px;
        border: 1px solid rgb(179, 176, 176);
        background-color: transparent;
        resize: none;
    }
    
    
    .contactRe-right form textarea{
        padding: .5rem 1rem;
        border-radius: 1rem;
        outline: none;
        font-size: 14px;
        border: 1px solid rgb(179, 176, 176);
        resize: none;
        margin-bottom: .5rem;
    }
    
    
    .contactRe-right form button{
        padding: .5rem 1rem;
        background-color: #03AA6B;
        border: none;
        border-radius: 1rem;
    }
    
    
    .contact-map{
        margin-top: 4vh;
    }
}

