.property-display-model{
    width: var(--standard-width);
    margin: auto;
    
    height: 100%;
    
    overflow: hidden;
}

.property-details{
    width: 100%;
}

.property-display-model > h1{
    text-align: center;
    font-family: "Montserrat", sans-serif;
    font-size: 32px;
    margin-top: 80px;
    font-weight: 600;
    color: #03AA6B;
}

.project-display-main{
    
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    margin-top: 20px;
    gap: 1vw;
}

.project-display-left{
    width: 60%;
    height: 100%;
    
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.project-slider{
    width: 100%;
    height: var(--standard-height);
}

.mySwiper1{
    width: 100%;
    height: 100%;
}

.mySwiper1 img{
    width: 100%;
    height: 100%;
    object-fit: contain;
}


.property-details{
    line-height: 1.6rem;
    letter-spacing: 1px;
    font-size: 16px;
}

.property-contact{
    text-align: center;
    letter-spacing: 1px;

}

.project-display-right{
    width: 100%;
    height: 100%;
    padding: 16px 12px;
    border: 1px solid rgb(156, 156, 156);
    display: flex;
    border-radius: 1rem;
    flex-direction: column;
    gap: .5rem;
}

.property-display-contact{
    width: 33%;
    height: 100%;
}

.project-display-right > h2 {
    font-size: 20px;
    font-family: "Montserrat", sans-serif;
    font-weight: bolder;
    line-height: 1.6rem;
}

.project-display-right form{
    display: flex;
    flex-direction: column;
    font-size: 14px;
    gap: .5rem;
}

.project-display-right form label{
    line-height: 1.7rem;
    color: rgb(88, 88, 88);
}


.project-display-right form input{
    padding: .3rem 1rem;
    letter-spacing: .4px;
    border: 1px solid rgb(156, 156, 156);
    border-radius: .5rem;
    background-color: transparent;
    outline: none;
}

.project-display-right form textarea{
    resize: none;
    width: 100%;
    height: 300px;
    letter-spacing: .4px;
    padding: .3rem 1rem;
    border: 1px solid rgb(156, 156, 156);
    border-radius: .5rem;
    background-color: transparent;
}

.project-display-right form button{
    padding: .5rem 1rem;
    background-color: #038352;
    outline: none;
    cursor: pointer;
    border: 1px solid #03794c;
    border-radius: .5rem;
}

.other-property-model{
    width: 100%;
   
    height: 100%;
    display: flex;
    flex-direction: column;
    gap:1rem;
    margin-top: 20px;
}

.cardRe.property{
    width: 100%;
}

.property-location-button{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: .5rem;
}





@media (max-width: 600px) {
    .property-display-model{
      
        padding: 15vh 4.1vw;
        
    }
    
    .property-display-model > h1{

        font-size: 1.3rem;
   
    }

    .map-width{
        padding: 0 1rem;
    }

   
    .project-display-main{
        display: flex;
        flex-wrap: nowrap;
        flex-direction: column;
        width: 100%;
        height: 100%;
        margin-top: 4vh;
        gap: 1vw;
    }
    
    .project-display-left{
        width: 100%;
        height: 100%;
        
        display: flex;
        flex-direction: column;
        gap: 3vh;
    }
    
    .project-slider{
        width: 100%;
        height: 80vh;
        margin-top: -15vh;
    }
    
    .mySwiper1{
        width: 100%;
        height: 100%;
    }
    
    .mySwiper1 img{
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
    
    
    .property-details{
        line-height: 2rem;
   
    }
    
    .property-contact{
        text-align: center;
    
        line-height: 2rem;
    }

    .property-display-contact{
        width: 100%;
        margin-top: 2rem;
        height: 100%;
    }
    

    
 
}