.propertylist-model{
    width: 100%;
    
    height: 100%;
    padding: 1rem 0;
}

.propertylist-model > h1{
    text-align: center;
    font-family: "Montserrat", sans-serif;
    font-size: 32px;
    color: #03AA6B;
    margin-top: 60px;
    width: 100%;
    font-weight: 600;
}

.properties-container{
    
    width: var(--standard-width);
    margin:40px auto;
    justify-content: space-between;
    gap: 1rem;
    /* margin-top: 50px; */
    display: flex;
    flex-wrap: wrap;
}

@media (max-width: 600px) {
    .propertylist-model{
        width: 100%;
        min-height: 100vh;
        height: 100%;
        padding: 16vh 4.1vw;
    }
    
    .propertylist-model > h1{
        text-align: center;
        font-family: "Montserrat", sans-serif;
        font-size: 5vh;
        color: #03AA6B;
    }
    
    .properties-container{
        margin-top: 4vh;
        width: 100%;
        height: 100%;
        justify-content: space-between;
        gap: 1rem;
        display: flex;
        flex-wrap: wrap;
    }
}