.aboutus-model{
    
    width: var(--standard-width);
    margin: auto;
    height: 100%;
    padding: 1rem 0;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.aboutRe-top{
  margin-top: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.aboutRe-top h1{
  font-family: "Montserrat", sans-serif;
  font-size: 40px;
  text-align: center;
  letter-spacing: -1px;
  font-weight: 500;
  width: 65%;
  color: black;
}

.aboutRe-top p{
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  text-align: center;
  font-weight: lighter;
  letter-spacing: -.5px;
  width: 65%;
  color: black;
}

.aboutRe-bottom{
  width: 100%;
  height: 100%;
  display: flex;
  gap: 1rem;

  flex-wrap: wrap;
  justify-content: space-between;
}

.aboutRe-profile{
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 23%;
  
  
}


.aboutRe-profile img{
  width: 100%;
  margin-bottom: .5rem;
  height: 15rem;
  border-radius: 1rem;
}

.aboutRe-profile h3{
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
}

.aboutRe-profile p{
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  letter-spacing: -.5px;
 
  font-weight: lighter;
}


@media (max-width: 600px) {
  .aboutus-model{
    min-height: 100vh;
    width: 100%;
    height: 100%;
    padding: 16vh 4.1vw 2rem 4.1vw;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.aboutRe-top{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}

.aboutRe-top h1{
  font-family: "Montserrat", sans-serif;
  font-size: 32px;
  text-align: center;
  letter-spacing: -1px;
  font-weight: 500;
  width: 100%;
  color: black;
}

.aboutRe-top p{
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  text-align: center;
  font-weight: lighter;
  letter-spacing: -.5px;
  width: 100%;
  color: black;
}

.aboutRe-bottom{
  width: 100%;
  height: 100%;
  display: flex;
  gap: 1rem;

  flex-wrap: wrap;
  justify-content: space-between;
}

.aboutRe-profile{
  display: flex;
  flex-direction: column;
  height: 100%;
  width: 45%;
  
  
}


.aboutRe-profile img{
  width: 100%;
  margin-bottom: .5rem;
  height: 15rem;
  border-radius: 1rem;
}

.aboutRe-profile h3{
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
}

.aboutRe-profile p{
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  letter-spacing: -.5px;
  font-weight: lighter;
}
}