.headerRe-main{
    position: relative;
}

.headerRe-container{
    height: 90px !important;
    width: var(--standard-width);
    left: 50%;
    margin: 0 !important;
    /* padding: 0 12vw; */
    transform: translateX(-50%);
    /* position: fixed ;
    z-index: 100; */
    position: fixed;
    z-index: 100;
    /* background-color: rgb(255, 255, 255); */
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.headerRe-logo{
    width: 5rem;
    height: 5rem;
    cursor: pointer;
}

.headerRe-left{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 3rem;
}

.headerRe-left a{
    font-family: var(--font-pop);
    letter-spacing: -.5px;
    font-size: 14px;
    font-weight: 500;
    position: relative; 
    cursor: pointer;
    
    text-decoration: none;
    color: black !important;
}

.navLinks{
    font-family: var(--font-pop);
    letter-spacing: -.5px;
    font-size: 14px;
    font-weight: 500;
    position: relative; 
    cursor: pointer;
    text-decoration: none;
    color: black;
}

.product {
    position: relative;
}


.dropdown-content {
    width: max-content;
    height: auto;
    padding: 2rem 0rem;
    background: #f2f2f2;
    z-index: 10;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
   
    display: flex;
    flex-direction: column;
    gap: .5rem;
    position: absolute;
    top: 15px;
    opacity: 0;  /* Initial opacity set to 0 */
    transition: opacity 0.3s ease-in-out;  /* Add transition for smooth animation */
}

.product:hover .dropdown-content {
    opacity: 1;  /* Change opacity to 1 on hover */
}

.dropdown-content a {
    color: rgb(0, 0, 0) ;
    padding: 5px;
    transition: background-color 0.3s ease-in-out;  /* Add transition for smooth background color change */
}

.dropdown-content a:hover {
    background-color: rgba(194, 194, 194, 0.747);
}


.headerRe-left a:after {
    content: "";
    position: absolute;
    width: 100%;
    transform: scaleX(0);
    height: .3vh;
    bottom: -15%;
    left: 0;
    background-color: #03AA6B;
  
    transform-origin: bottom right;
    transition: transform 0.25s ease-out;
  }
  
  .headerRe-left a:hover:after { /* Changed selector */
    transform: scaleX(1);
    transform-origin: bottom left;
  }

.headerRe-left button{
    color: white;
    background-color: #04794c;
    border: none;
    font-family: var(--font-pop);
    letter-spacing: -.5px;
    font-size: 14px;
    border-radius: .5rem;
    padding: .5rem 2rem;
    outline: none;
}