.blogs-display-container{
    width: 100%;
    min-height: 100vh;
    padding: 1rem 12vw;
    display: flex;
    flex-direction: column;
    gap: .5rem;
    height: 100%;
}


.blogs-display-container h1{
    color: black;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    font-size: 20px;
    width: 50%;
}

.blogs-display-right > h3{
    font-family: "Montserrat", sans-serif;
    font-size: 20px;
}

.blogs-display-main{
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-between;
}


.blogs-display-left {
    width: 60%;
    display: flex;
    flex-direction: column;
    gap: 1rem;

}

.blogs-display-left img{
    width: 100%;
    height: 25rem;
    cursor: pointer;
   
    object-fit: contain;
}

.blogs-display-right{
    width: 35%;
    height: 100%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.blog-display-text{
     white-space: pre-wrap;
}



@media (max-width: 600px) {
    .blogs-display-container{
        width: 100%;
        min-height: 100vh;
        padding: 16vh 4.1vw 1rem 4.1vw;
        display: flex;
        flex-direction: column;
        gap: .5rem;
        height: 100%;
    }
    
    
    .blogs-display-container h1{
        color: black;
        font-family: "Montserrat", sans-serif;
        font-weight: 600;
        font-size: 20px;
        width: 100%;
    }
    
    .blogs-display-right > h3{
        font-family: "Montserrat", sans-serif;
        font-size: 20px;
    }
    
    .blogs-display-main{
        display: flex;
        flex-wrap: wrap;
        gap: 2rem;
        width: 100%;
        height: 100%;
        justify-content: space-between;
    }
    
    
    .blogs-display-left {
        width: 100%;
        display: flex;
        flex-direction: column;
        gap: 1rem;
    
    }
    
    .blogs-display-left img{
        width: 100%;
        height: 25rem;
        cursor: pointer;
       
        object-fit: contain;
    }
    
    .blogs-display-right{
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
    
    .blog-display-text{
         white-space: pre-wrap;
    }
    

}