.area-converter-model{
    height: 100%;
    width: 100%;
    /* min-height: 100vh; */
    padding: 1rem;
}

.area-converter-container{
  width: var(--standard-width);
  margin: 60px auto;
}

.area-converter-container > h1{
    text-align: center;
    font-family: "Montserrat", sans-serif;
    font-size: 32px;
    width: 100%;

    font-weight: 600;
    color: #03AA6B;
}


.Converter-title{
  margin: 1rem auto 0 auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  justify-content: center;
  background-color: #03aa6a8e;
  padding: .5rem 2rem;
  width: max-content;
  align-items: center;
}

.Converter-title > h3{
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  display: flex;
  align-items: center;
  gap: 1rem;
  color: black;
}






  
  .input-section {
    margin-top: 4vh;
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
  }
  
  .input-group {
    display: flex;
    flex-direction: column;
    letter-spacing: 1px;
    gap: 10px;
  }
  
  .form-control {
    padding: .3rem 1rem;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 2rem;
  }
  
  .result-section {
    margin-bottom: 20px;
  }
  
  .result-group {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 10px;
  }
  
  .result-group h2 {
    margin: 0;
    letter-spacing: 1px;
  }

  .input-group input{
    letter-spacing: 1px;
  }

  .result-group input{
    letter-spacing: 1px;
  }
  
  .button-group {
    display: flex;
    justify-content: space-between;
  }
  
  .convert-button, .clear-button {
    padding: 10px 20px;
    font-size: 1em;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }

  .convert-button, .calc-button {
    padding: 10px 20px;
    font-size: 1em;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }


  .calc-button {
    background-color: #03AA6B;
    color: white;
  }
  
  .input-group input{
    padding: .3rem 1rem;
    font-size: 14px;
    border: 1px solid rgb(190, 190, 190);
    outline: none;
    border-radius: 2rem !important;
  }

  .input-group label{
    margin-left: .5rem !important;
    font-size: 14px;
    
  }

  
  .convert-button {
    background-color: #007bff;
    color: white;
  }
  
  .clear-button {
    background-color: #dc3545;
    color: white;
  }
  
  @media (max-width: 600px) {
    .area-converter-model{
     
        padding: 16vh 4.1vw;
    }
    
    .area-converter-container{
  width: 100%;
  margin: none;
}
    
