    .Converter-title > h3{
      text-align: center;
      font-family: "Montserrat", sans-serif;
      font-size: .8rem;
      display: flex;
      align-items: center;
      gap: 1rem;
      color: black;
    }
    
    
    
      
      .input-section {
        margin-top: 4vh;
