/******** footer ********/

.footer-main{
    width: 100%;
    height: 100%;
    color: black;
    background-color: #f2f2f2; /* Updated background color for a modern look */
}
.footer {
    width: var(--standard-width);
    padding: 2rem 0;
    margin: auto;
    
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

.footerRe-logo{
    display: flex;
    flex-direction: column;
   
}

.footerRe-logo img{
  width: 6rem;
  height: 6rem;

}

.footerRe-logo p{
  width: 15rem;
  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  color: #616161;
  letter-spacing: .3px;
}

.footerRe-logo h3 {
  font-size: 16px;
  letter-spacing: -1px;
  color: #121212;
  font-family: "Montserrat", sans-serif;
  font-weight: bolder;
}

.footerRe-icons{
  display: flex;
  gap: 1rem;
  color: #504f4f;
}

.footerRe-i{
  width: 1.5rem;
  height: 1.5rem;
}

.footerRe-company h3, .footerRe-explore h3, .footerRe-social h3{
  font-size: 16px;
  letter-spacing: -1px;
  color: #121212;
  font-family: "Montserrat", sans-serif;
  font-weight: bolder;
}

.footerRe-company h4, .footerRe-explore h4{
  font-size: 14px;
  cursor: pointer;
  letter-spacing: -1px;
  position: relative;
  color: #616161;
  font-weight: light;
  font-family: "Montserrat", sans-serif;

}

.bottom-footer-main{
  background-color: #5f5f5f;
  width: 100%;
}

.bottom-footerRe{
  width: var(--standard-width);
  height: 100%;
  margin: auto;
  padding: 1rem 0vw .5rem 0vw;
  display: flex;
  justify-content: space-between;
  
}

.bottom-footerRe h3{
  color: rgb(255, 255, 255);
  font-size: 14px;

}

.bottom-footerRe span{
  color: #7EE081;
  letter-spacing: .5px;
 
}

.footerRe-icons a{
  text-decoration: none;
  color: #504f4f;
  padding: 0;
  margin: 0;
}

@media (max-width: 600px) {
  .footer {
    width: 100%;
    height: 100%;
    color: black;
    background-color: #f2f2f2; /* Updated background color for a modern look */
    padding: 1rem 4.1vw;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  .footerRe-logo p{
    width: 100%;
    font-family: "Montserrat", sans-serif;
    font-size: 12px;
    color: #616161;
    letter-spacing: .3px;
  }

  .bottom-footerRe{
    width: 100%;
    height: 100%;
    padding: 1rem 4.1vw .5rem 4.1vw;
    display: flex;
    justify-content: space-between;
    background-color: #5f5f5f;
  }
}






