.EMI-model{
    width: 100%;
    height: 100%;
    /* min-height: 100vh; */
    padding: 1rem ;
}

.EMI-model > h1{
    text-align: center;
    font-family: "Montserrat", sans-serif;
    font-weight: 600;
    width: 100%;
    margin-top: 60px;
    font-size: 32px;
    color: #03AA6B;
}


 
  .calculator {
    width: var(--standard-width);
    margin:60px auto;
    
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .calculator label {
    margin-bottom: 5px;
    font-size: 14px;
  }
  
  .calculator input, .calculator select {
    padding: .3rem 1rem;
    font-size: 14px;
    border: 1px solid #ccc;
    letter-spacing: 1px;
    border-radius: 2rem;
  }
  
  .calculator button {
    padding: .3rem 1rem;
    font-size: 14px;
    letter-spacing: 1px;
    background-color: #03AA6B;
    border: none;
    border-radius: .5rem;
    margin-top: 1rem;
    cursor: pointer;
  }
  
  .calculator button:hover {
    background-color: #04c078;
  }
  
  #result {
    margin-top: 20px;
    text-align: center;
  }
  
  #result p {
    margin: 5px 0;
    letter-spacing: 1px;
    font-weight: bold;
  }

  @media (max-width: 600px) {
    .EMI-model{
        width: 100%;
        height: 100%;
        min-height: 100vh;
        padding: 16vh 4.1vw;
    }

    
  .calculator {
    width: 100%;
    margin:40px auto;
    
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
    
    .EMI-model > h1{
        
        font-size: 1.8rem;
       
    }
    
  }
