.blogs-container{
    
    height: 100%;
    width: var(--standard-width);
    margin: auto;
    display: flex;
    padding: 1rem 0;
    flex-direction: column;
    gap: 1rem;
}

.blogs-container h1{
    width: 100%;
    margin-top: 60px;
    color: #03AA6B;
    font-family: "Montserrat", sans-serif;
    font-size: 32px;
    font-weight: 600;
    text-align: center;
}

.blogs-items{
    
  
    width: 100%;
    height: 100%;
    
    display: grid;
    gap: 2rem;
    
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: minmax(450px, auto);
    grid-auto-flow: dense;
}

.blogs-box{
    width: 100%;
    height: 12rem;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.blogs-box img{
    width: 100%;
    height: 12rem;
}

.blog-text{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
    padding: 2rem .5rem 1rem .5rem;
    height: 100%;
    gap: .5rem;
    background: #f2f2f2;
}

.blog-text button{
    color: white;
    background-color: #04794c;
    border: none;
    font-family: "Montserrat", sans-serif;
    letter-spacing: -.5px;
    font-size: 13px;
    border-radius: .5rem;
    padding: .5rem 2rem;
    outline: none;
}


.blog-text h2{
    font-size: 14px;
    font-family: "Montserrat", sans-serif;
}

 @media (max-width: 600px) {
    .blogs-container{
        min-height: 100vh;
        height: 100%;
        width: 100%;
        display: flex;
        padding: 16vh 4.1vw 1rem 4.1vw;
        flex-direction: column;
        gap: 1rem;
    }
    

    
    .blogs-items{
        
      
        width: 100%;
        height: 100%;
        
        display: grid;
        gap: 2rem;
        
        grid-template-columns: repeat(2, 1fr);
        grid-auto-rows: minmax(450px, auto);
        grid-auto-flow: dense;
    }
    
  
 }