@import url('https://fonts.googleapis.com/css?family=Pirata+One|Rubik:900');

.home-page-model{
  width: 100%;
  /* margin-top: 10px; */
  /* margin-top: 90px; */
  height: 100%;
  background-color: #FFFFFC;
}
.home-model {
  position: relative;
  min-height: 600px;
  /* margin-top: -100px; */
  
  /* padding: 2rem 12vw; */
  width: 100%;
  display: flex;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), 
  url(../../assests/homepage4.jpg);
  align-items: flex-end;

  background-size: 100% 100%;
  background-position: center center;
  background-repeat: no-repeat;

  filter: grayscale(80%);
}

.home-remodel-text{
  display: flex;
  width: var(--standard-width);
  /* min-width: 80%; */
  margin: 0 auto;
  margin-bottom: 40px;
  flex-direction: column;
  gap: .5rem;
}

.home-remodel-text h2, h1{
  font-size: 48px;
  letter-spacing: .5px;
  width: 60%;
  font-family: var(--font-formula-bold);
  text-transform: uppercase;
  font-weight: 900;
  color: white;
  
}

.home-remodel-text button{
  width: 35%;
  gap: .3rem;
  display: flex;
  border: 1px solid white;
  background-color: transparent;
  color: white;
  padding: .5rem;  
  cursor: pointer;
  font-size: 16px;
  letter-spacing: -.5px;
  border-radius: .5rem;
  outline: none;
  justify-content: center;
  align-items: center;
}





.mySwiper2{
  width: 100%;
  height: 100%;
}

.mySwiper2 img{
  width: 100%;
  min-height: 100vh;
    height: 100%;
}




.new-arrival-model{
    
    min-height: 100%;
    width: var(--standard-width);
    /* padding: 4rem 12vw 0rem 12vw; */
    display: flex;
    
    margin: auto;
    margin-top: 100px;
    flex-direction: column;
    gap: 1rem;
    
}

.cardRe.sale{
  position: relative;
}

.cardRe.sale::before {
  content: '';
  position: absolute;
  top: 10%;
  left: 0;
  width: 100%;
  height: 80%;
  
  background: url('../../assests/soldout.png');
  opacity: .8;
   background-size: 100% 100%;
  background-position: center center;
  background-repeat: no-repeat;
  
  /* Adjust the colors and opacity as needed */
  z-index: 10; /* Ensures the gradient stays on top of the image */
  border-top-right-radius: 1.5rem;
  border-top-left-radius: 1.5rem;
  pointer-events: none; /* Allows clicking through the gradient */
}


.new-arrival-model > h2{
    text-align: center;
    font-size: 36px;
   
    font-weight:bolder;
    color: black;

}

.new-arrival-model > p{
  text-align: center;
  font-size: 14px;
  font-weight: bolder;
  color: rgba(0, 0, 0, 0.541);

}

.home-model-banner{
  width: var(--standard-width);
  margin: 100px auto;
  height: 100%;
  

  
}

.banner-container{
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #E5E6FA;
  padding: .5rem;
  border-radius: 1.5rem;
  display: flex;
  justify-content: space-between;
}



.banner-container img{
  width: 30%;
  height: 20rem;
  
}

.banner-content{
  display: flex;
  flex-direction: column;
  width: 70%;
  justify-content: center;
  padding: 0 1.5rem;
  gap: .5rem;
}

.banner-container h2{
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
  font-weight: bold;
}

.banner-container p{
  color: rgb(87, 85, 85);
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
  line-height: 1.6rem;
  font-weight: lighter;

}

.homeRe-service{

  height: 100%;
  width: var(--standard-width);
  margin: auto;
  display: flex;
  margin-top: 100px;
  justify-content: space-between;
  color: #03AA6B;
}

.accordian-paragraph{
  font-size: 14px;
  font-family: "Montserrat", sans-serif;
  line-height: 1.5rem;
}

.serviceRe-left{
  width: 40%;
  height: 100%;

  display: flex;
  flex-direction: column;
  gap: .5rem;
  padding: 1rem .5rem;
}

strong{
  color: #df6579;
}

.serviceRe-right{
  width: 50%;
  height: 100%;
 
}

.serviceRe-right img{
  width: 100%;
  height: 30rem;
  border-radius: 2rem;
}

.homeRe-service h2{
  font-size: 36px;
  color: black;
  font-weight: bolder;
}

.homeRe-service p{
  font-size: 14px;
  color: rgba(0, 0, 0, 0.616);
  
}
/* ---------------------------------------------Horizontal Card-------------------------------------------------------------- */



.center {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: center;
  }

  .wrapperRe{
    display: flex;
    height: 100%;
  
    width: 100%;
    overflow: hidden;
    justify-content: space-between;
  }

  .cardRe{
    width: 31%;
    height: 550px;
    padding: .5rem;
    position: relative;
    display: flex;
    flex-direction: column;
    gap: .5rem;
    border: .1px solid rgb(219, 218, 218);
    
    border-radius: 24px;
  }

  .locationRe{
    display: flex;
    justify-content: space-between;

  }

  .locationRe h3{
    color: black;
    font-size: 18px;
    font-family: "Montserrat", sans-serif;
    font-weight: bolder;
    letter-spacing: -.5px;
  }

  .locationIcon{
    color: #1877F2;
  }


 

  hr{
    padding: 0rem;
    margin: 0rem;
  }


  .descRe p {
    position: relative;
    padding-left: 1rem; /* Adds padding to create space for the bullet */
    color: rgb(78, 76, 76);
    font-size: 14px;
    letter-spacing: -.5px;
   
    font-family: "Montserrat", sans-serif;
    margin-bottom: .5rem; /* Adds spacing between each point */
}



.descRe p::before {
    content: '•'; /* Custom bullet point */
    position: absolute;
    left: 0;
    color: #333; /* Color of the bullet point */
    font-size: 1.2rem; /* Size of the bullet */
    line-height: 1; /* Ensures alignment with text */
}

  .cardRe::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 55%;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.158), transparent);
    /* Adjust the colors and opacity as needed */
    z-index: 1; /* Ensures the gradient stays on top of the image */
    border-top-right-radius: 1.5rem;
    border-top-left-radius: 1.5rem;
    pointer-events: none; /* Allows clicking through the gradient */
}

  .cardRe img{
    width: 100%;
    height: 55%;
    border-top-right-radius: 1.5rem;
    
    border-top-left-radius: 1.5rem;
  }

  .cardRe-buttons{
 
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
  }

  .cardRe-buttons button{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .5rem;
    width: 45%;
    font-size: 13px;
    border-radius: .3rem;

    padding: .5rem 0rem;
  }

  .white-btn{
    border: 1px solid rgb(129, 128, 128);
    background-color: transparent;
    outline: none;
  }

  .primary-btn{
    border: none;
    outline: none;
    border: 1px solid #058151;
    color: white;
    background-color: #058151;
  }
  


  .mySwiper{
    width: 100%;
    height: 100%;
  }

  .mySwiper img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  
  
  .map {
    display: flex;
    margin-top: 1em;
    gap: 2vw;
  }
  
  .map > button {
    all: unset;
    background: #133a58;
    cursor: pointer;
    margin: 0 0.125em;
    position: relative;
    user-select: none;
    border-radius: 100%;
    height: 1em;
    width: 1em;
  }
  
  .map > button.active:after {
    background: #001d38;
    content: "";
    left: 50%;
    position: absolute;
    transform: translate(-50%, -50%);
    top: 50%;
    border-radius: 100%;
    height: 0.5em;
    width: 0.5em;
  }


