
  
.mapdand-model{
    
    height: 100%;
    width: var(--standard-width);
    margin: auto;
    display: flex;
    flex-direction: column;
    
    /* flex-wrap: wrap; */
    position: relative;
}

.mapdand-content{
    width: 100%;
    /* min-height: 100vh; */
    height: 100%;
    z-index: 10;
    background-color: white;
    margin-top: 40px;
    
}

.mapdand-content h1{
  color: #03AA6B;
  width: 100%;
  font-family: var(--font-formula-bold);
  font-size: 32px;
  text-align: center;
  
}

.mapdand-content p{
  margin-top: 1rem;
  margin-bottom: 40px;
  font-size: 16px;
  font-family: var(--font-pop);
  line-height: 1.65rem;
  color: rgb(114, 114, 114);
}

.mapdand-content p span{
  margin-top: 1rem;
  
  font-size: 16px;
  line-height: 1.2rem;
  color: red;
}
  /* height of the container */
  .leaflet-container {
    height: 45rem;
    width: 100%;
    z-index: 2;
    border-radius: 1rem;
    position: relative;
    /* border-radius: 2rem; */
 
  }

  .custom-vertex-icon{
    width: 5px;
    height: 5px;
    border-radius: 50%;
    background-color: red;
  }

  .search-bar-map {
    position: absolute !important;
    top: 10px; /* Adjust the top position as needed */
    left: 50%; /* Center horizontally */
    transform: translateX(-50%); /* Adjust for centering */
    z-index: 1000; /* Ensure it's above other elements */
    border: 1px solid black;
    width: 300px; /* Adjust width as needed */
    background-color: white; /* Optional: Set background color */
    padding: 5px; /* Optional: Add padding for better appearance */
  }
  .cluster-icon {
    background-color: #333;
    height: 1em;
    width: 1em;
    border-radius: 50%;
    border: 1px solid black;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    font-size: 1.2rem;
    box-shadow: 0 0 0px 5px #fff;
  }

  .polygon-control{
    position: absolute !important;
    top: 10vh;
  }

  .leaflet-control-measure {
    /* position: absolute !important;
    
    top: 10vh !important;
    z-index: 200;
    left: 60vw !important; */
    z-index: 200 !important; 
  }
  
  
